:root {
  --background-color: gainsboro;
  --header-color: aqua;
  --active-color: aliceblue;
  --text-color: black;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --tm: .25s;
}

html {
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
  background-color: var(--background-color);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

img {
  width: 100%;
  height: 320px;
  object-fit: cover;
  display: block;
}

button {
  width: 70px;
  height: 70px;
  background-color: var(--background-color);
  cursor: pointer;
  transition: color var(--tm) var(--cubic), background-color var(--tm) var(--cubic), box-shadow var(--tm) var(--cubic);
  border: none;
  border-radius: 50%;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
}

button:hover, button:focus {
  color: var(--text-color);
  background-color: var(--active-color);
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

.container {
  padding: 0 15px;
}

.go-top-btn {
  opacity: .6;
  position: fixed;
  bottom: 30px;
  right: 40px;
}

.header {
  z-index: 10;
  width: 100vw;
  background-color: var(--header-color);
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px #0009;
}

.header-container {
  align-items: center;
  display: flex;
}

.set-block {
  width: 20%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.filter {
  flex-direction: column;
  display: flex;
}

.search-form {
  width: 60%;
  justify-content: space-between;
  align-items: center;
  margin-right: 50px;
  display: flex;
}

input {
  background-color: var(--background-color);
  transition: color var(--tm) var(--cubic), background-color var(--tm) var(--cubic), box-shadow var(--tm) var(--cubic);
  border-style: none;
  border-radius: 5px;
  outline: none;
  padding-left: 15px;
}

input:hover, input:focus {
  color: var(--text-color);
  background-color: var(--active-color);
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

input[type="checkbox"] {
  filter: hue-rotate(340deg) brightness(1.7);
}

input[name="per_page"] {
  width: 5ch;
  font-size: 18px;
}

input[name="searchQuery"] {
  width: 400px;
  height: 40px;
  margin-right: 15px;
  font-size: 25px;
  font-weight: 500;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 115px;
  display: flex;
}

.photo-card {
  max-width: 25%;
  transition: transform var(--tm) var(--cubic);
  flex: 20%;
}

.photo-card:hover {
  transform: scale(1.05);
}

.info {
  background-color: var(--active-color);
  cursor: pointer;
  border-radius: 0 0 5px 5px;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

